
.home-btn {
    background-color: #fbeee0;
    border: 2px solid #422800;
    border-radius: 30px;
    box-shadow: #422800 4px 4px 0 0;
    color: #422800;
    cursor: pointer;
    display: inline-block;
    font-weight: 600;
    font-size: 18px;
    padding: 0 18px;
    line-height: 50px;
    text-align: center;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
  }
  
  .home-btn:hover {
    background-color: #fff;
  }
  
  .home-btn:active {
    box-shadow: #422800 2px 2px 0 0;
    transform: translate(2px, 2px);
  }
  
  @media (min-width: 768px) {
    .home-btn {
      min-width: 120px;
      padding: 0 25px;
    }
  }

  .one {
    border-radius: 30px 0 0 30px;
  }
  .two {
    border-radius:  0 30px 30px 0;
  }

.interface {
  right: 1%;
  top: 0.5%;
  position: absolute
}

@media (max-width: 980px) {
  .interface {
    display: flex;
    position: static;
    justify-content: center;
    align-items: center;
    margin-top: 1%;
    margin-bottom: 8%;
  }

  .home-btn {
    font-size: 1.1rem;
  }
}